#veg-only-chkbox[type="checkbox"] {
  appearance: none;
  cursor: pointer;
  background: #ccc;
  border: 3px solid #ccc;
  border-radius: 6px;
  width: 40px;
  height: 20px;
  transition: all .4s;
  position: relative;
}

#veg-only-chkbox:checked[type="checkbox"] {
  background-color: #3d9b6d;
  border: 2px solid #3d9b6d;
}

#veg-only-chkbox[type="checkbox"]:after {
  content: "";
  background: #fff;
  border-radius: 4px;
  width: 50%;
  height: 100%;
  transition: all .4s;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(.9);
}

.veg-wrapper {
  align-items: center;
  margin-bottom: 1rem;
  display: flex;
}

.veg-wrapper label {
  color: #494949;
  font-size: .8rem;
  font-weight: bold;
}

#veg-only-chkbox:checked[type="checkbox"]:after {
  content: "";
  left: unset;
  background-color: #fff;
  right: 0;
}

#veg-only-chkbox:checked[type="checkbox"]:before {
  content: "";
  z-index: 100;
  background-color: #3d9b6d;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  position: absolute;
  top: calc(50% - 4px);
  right: calc(25% - 4px);
}

